@import "../../assets/iconfonts/iconfont.css";
.pro-container {
  padding-top: 1.173333rem;
}
.pro-container .pro-list-con {
  padding-top: 0.4rem;
}
.pro-container .pro-list-con .loading-tip {
  font-size: 0.48rem;
  color: #999;
  text-align: center;
}
.pro-container .pro-list-ul {
  background-color: #fff;
}
.pro-container .pro-list-ul .pro-item {
  min-height: 1.866667rem;
  padding: 0.4rem;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.pro-container .pro-list-ul .pro-item .pro-item-select {
  display: flex;
  justify-content: center;
  align-items: center;
}
.pro-container .pro-list-ul .pro-item .pro-item-select .pro-select-status {
  font-size: 0.6rem;
  color: #ccc;
  text-align: left;
}
.pro-container .pro-list-ul .pro-item .pro-item-select .pro-selected {
  color: #975ec9;
}
.pro-container .pro-list-ul .pro-item .pro-item-select .pro-name {
  font-size: 0.48rem;
  color: black;
  text-align: left;
  margin-left: 0.266667rem;
  margin-top: 0.16rem;
}
.pro-container .pro-list-ul .pro-item .pro-item-edit {
  display: flex;
  justify-content: center;
  align-items: center;
}
.pro-container .pro-list-ul .pro-item .pro-item-edit .icon-jian {
  font-size: 0.666667rem;
  color: #ccc;
  text-align: left;
}
.pro-container .pro-list-ul .pro-item .pro-item-edit .pro-num {
  font-size: 0.4rem;
  color: black;
  text-align: center;
  min-width: 0.8rem;
}
.pro-container .pro-list-ul .pro-item .pro-item-edit .icon-jia {
  font-size: 0.666667rem;
  color: #975ec9;
  text-align: left;
}
.pro-container .pro-list-ul .pro-item .pro-item-edit .edit-active {
  color: #975ec9;
}
